<template>
    <div class="morestores-box">
      <common-header :tittle="tittle" :showleft="true" :showinput="true" :showright="true" :routerLeft="routerLeft" :carProductCd="carProductCd"></common-header>
<!--      <el-amap-->
<!--                vid="amapContainer"-->
<!--               :center="center"-->
<!--                :showPositionPoint="true"-->
<!--               :zoom="zoom"-->
<!--               class="amap-demo"-->
<!--            >-->
<!--        position: [{{ lng }}, {{ lat }}] address: {{ address }}-->
<!--      </el-amap>-->
      <div class="morestores-list">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1"><span @click="tabChange('1')">综合排序</span></mt-tab-item>
          <mt-tab-item id="2"><span @click="tabChange('2')">销量排行</span></mt-tab-item>
          <mt-tab-item id="3"> <span @click="tabChange('3')">离我最近</span></mt-tab-item>
        </mt-navbar>
        <!-- tab-container -->
        <mt-tab-container v-model="selected">
          <mt-tab-container-item id="1">
            <div class="content-tab">
              <li v-for="item in stores1List" @click="toSelectStore(item.partnerId,item.partnerNm,item.partnerLinkPhone)">
                <div class="top">
                  <div class="top-left">
                    <img :src="imgFileUrl+item.partnerPic" alt="">
                  </div>
                  <div class="top-right">
                    <h5>{{item.partnerNm}}</h5>
                    <p>{{item.partnerAddress}}</p>
                    <p>
                      <span><van-icon name="location" color="#999" size="0.45rem" tag="i" />{{item.distance}}km&nbsp;&nbsp;&nbsp;售{{item.saleArea}}</span>
                      <span>已售{{item.saleNum}}辆</span>
                    </p>
                  </div>
                </div>
                <div class="bottom">
                    <p @click="phone">咨询电话{{item.partnerLinkPhone}}</p>
                </div>
              </li>
            </div>
            <div class="footer">
              <p>共{{stores1List.length}}家门店<!--<van-icon :name="isShow?'arrow-down':'arrow-up'" />--></p>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="content-tab">
              <li v-for="item in stores2List" @click="toSelectStore(item.partnerId,item.partnerNm,item.partnerLinkPhone)">
                <div class="top">
                  <div class="top-left">
                    <img :src="imgFileUrl+item.partnerPic" alt="">
                  </div>
                  <div class="top-right">
                    <h5>{{item.partnerNm}}</h5>
                    <p>{{item.partnerAddress}}</p>
                    <p>
                      <span><van-icon name="location" color="#999" size="0.45rem" tag="i" />{{item.distance}}km&nbsp;&nbsp;&nbsp;售{{item.saleArea}}</span>
                      <span>{{item.saleNum}}辆</span>
                    </p>
                  </div>
                </div>
                <div class="bottom">
                  <p>咨询电话{{item.partnerLinkPhone}}</p>
                </div>
              </li>
            </div>
            <div class="footer">
              <p>共{{stores2List.length}}家门店<!--<van-icon :name="isShow?'arrow-down':'arrow-up'" />--></p>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="3">
            <div class="content-tab">
              <li v-for="item in stores3List" @click="toSelectStore(item.partnerId,item.partnerNm,item.partnerLinkPhone)">
                <div class="top">
                  <div class="top-left">
                    <img :src="imgFileUrl+item.partnerPic" alt="">
                  </div>
                  <div class="top-right">
                    <h5>{{item.partnerNm}}</h5>
                    <p>{{item.partnerAddress}}</p>
                    <p>
                      <span><van-icon name="location" color="#999" size="0.45rem" tag="i" />{{item.distance}}km&nbsp;&nbsp;&nbsp;售{{item.saleArea}}</span>
                      <span>{{item.saleNum}}辆</span>
                    </p>
                  </div>
                </div>
                <div class="bottom">
                  <p>咨询电话{{item.partnerLinkPhone}}</p>
                </div>
              </li>
            </div>
            <div class="footer">
              <p>共{{stores3List.length}}家门店<!--<van-icon :name="isShow?'arrow-down':'arrow-up'" />--></p>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </div>
</template>

<script>
import img1 from '../../assets/image/car1.jpg'
import * as homeApi from '../../api/home-api'
import { imgFileUrl } from '../../apiconfig/index'
import { Toast } from 'vant'
import commonHeader from 'common/common-header'
import VueAMap from 'vue-amap'
import { lazyAMapApiLoaderInstance } from 'vue-amap';
VueAMap.initAMapApiLoader({
  key: '3017ed7b349d804dc52fa6279a364c7e1',
  plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType' ,'AMap.Geocoder','AMap.LngLat'] ,
  v: '1.4.4'
});
lazyAMapApiLoaderInstance.load().then(() => {
  // your code ...
  this.map = new AMap.Map('amapContainer', {
    center: new AMap.LngLat(121.59996, 31.197646),
    resizeEnable: true
  });
});
export default {
  name: 'morestores',
  components: {
    commonHeader
  },
  data() {
    return {
      zoom: 12,
      center: [121.59996, 31.197646],
      address: '',
      lng: 0,
      lat: 0,
      tittle: '',
      routerLeft: '/orderinfodetail',
      imgFileUrl: imgFileUrl,
      selected: '1',
      storeName: '',
      isShow: true,
      page: {
        pageNumber: 1,
        pageSize: 10
      },
      carProductCd: '',
      src1: img1,
      stores1List: [],
      stores2List: [],
      stores3List: [],
      ruleForm: {
        name: '',
        phone: '',
        addr: '',
        long: '',
        lat: '',
        start_work_time: '',
        end_work_time: '',
      },
    }
  },
  mounted() {
    this.loadMap()
    map.on('complete', function() {
      alert('2222')
    });
    this.carProductCd = this.$route.params.carProductCd
    console.log('carProductCd==' + this.carProductCd)
    let data = {
      pageNumber: 1,
      pageSize: 10,
      longitude: '116.3424590000',
      latitude: '40.0497810000',
      type: '1',
      carId: this.$route.params.carProductCd,
      storeName: this.storeName
    }
    homeApi.getAllDealersByCarId(data).then(res => {
      if (res.data.code == 200) {
        this.stores1List = res.data.page.list
      } else {
        Toast(res.data.msg)
      }
    }).catch(err => {
      console.log(err)
    })
  },
  created() {
    this.getStore1List()
  },
  methods: {
    loadMap(){
      let that = this
      map().then(AMap => {
        console.log('地图加载成功')
        that.marker,that.map = new AMap.Map('amapContainer', {
          resizeEnable: true,
          zoom: 11,
          // pitch:80,
          // rotation:-15,
          // // viewMode:'3D',
        })

        // this.getposition()
        // this.watchClick()
        // this.getLoad()

        // this.setPosition()
      }, e => {
        console.log('地图加载失败' ,e)
      })
    },
    showInfoClick(){
      console.log('ss')
    },
    phone(){
      window.location.href = 'tel://'
    },
    tabChange(name) {
      if (name == '1') {
        this.getStore1List()
      } else if (name == '2') {
        this.getStore2List()
      } else if (name == '3') {
        this.getStore3List()
      }
    },
    getStore1List() {
      // 1综合排序 2离我最近 3价格最低
      let data = {
        pageNumber: 1,
        pageSize: 10,
        longitude: '116.3424590000',
        latitude: '40.0497810000',
        type: '1',
        carId: localStorage.getItem('carProductCd'),
        storeName: this.storeName
      }
      homeApi.getAllDealersByCarId(data).then(res => {
        if (res.data.code == 200) {
          this.stores1List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getStore2List() {
      let data = {
        pageNumber: 1,
        pageSize: 10,
        longitude: '116.3424590000',
        latitude: '40.0497810000',
        type: '3',
        carId: localStorage.getItem('carProductCd'),
        storeName: this.storeName
      }
      homeApi.getAllDealersByCarId(data).then(res => {
        if (res.data.code == 200) {
          this.stores2List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getStore3List() {
      let data = {
        pageNumber: 1,
        pageSize: 10,
        longitude: '116.3424590000',
        latitude: '40.0497810000',
        type: '2',
        carId: localStorage.getItem('carProductCd'),
        storeName: this.storeName
      }
      console.log('data==' + JSON.stringify(data))
      homeApi.getAllDealersByCarId(data).then(res => {
        if (res.data.code == 200) {
          this.stores3List = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    toNews() {
      this.$router.push('/message')
    },
    toSelectStore(partnerId, partnerNm, partnerLinkPhone) {
      localStorage.setItem('partnerId', partnerId)
      localStorage.setItem('partnerNm', partnerNm)
      localStorage.setItem('partnerLinkPhone', partnerLinkPhone)
      this.$router.push({path: '/orderinfodetail', name: 'orderinfodetail', params: {carProductCd: this.carProductCd, comeFrom: 'children'}})
    }
  }
}
</script>

<style scoped lang="less">
  @import "~styles/index.less";
  @import "~styles/variable.less";
  .morestores-box{
    width: 100%;
    /*height: auto;*/
    /*padding: 0 0.1rem;*/

    .header {
    width: 100%;
    height: 1.35rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #dedede;
      padding: 0 0.2rem 0 0;
    li:first-child {
      .van-icon{
        font-size: 0.6rem;
      }
      .icon-back {
        position: absolute;
        display: inline-block;
        .w(50);
        .h(50);
        .top(25);
        background: url("../../assets/image/back.png") center no-repeat;
        background-size: 0.32rem 0.5rem;
      }
    }
    li {
      .input-box {
        position: relative;
        width: 92%;
        margin-left: 4%;
        .pl(30);
        .pr(30);
        .icon {
          position: absolute;
          display: inline-block;
          .w(30);
          .h(30);
          top: 53%;
          .mt(-18);
          .left(50);
          background-image: url("../../assets/image/search-f.png");
          background-size: cover;
        }
        .search {
          background-color: #f7f3f8;
          .b-radius(58);
          .lh(65);
          width: 100%;
          outline: none;
          .pl(65);
          .pr(65);
          .fs(30);
        }
      }
    }
    li:last-child {
      .van-icon{
        font-size: 0.54rem;
      }
      .header-right {
        position: absolute;
        display: inline-block;
        .w(50);
        .h(50);
        .right(10);
        .top(25);
        background: url("../../assets/image/message.png") center no-repeat;
        background-size: 0.6rem 0.6rem;
      }
    }
  }
    .morestores-list{
      width: 100%;
      height: auto;
      .fs(30);
      .mint-tab-item-label{
        span{
          .fs(30)
        }
      }
      .content-tab{
        .pt(20);
        .pl(30);
        .pr(30);
        margin-top:3px;
        background-color: #f5f5f5;
        li{
          .mb(20);
          background: #fff;
          border-radius: 5px;
          .top {
            width: 100%;
            display: flex;
            justify-content: flex-start;
            text-align: left;
            align-items: center;
            border-bottom: 1px solid #dedede;
            .pt(25);
            .pl(25);
            .pr(25);
            .pb(17);
            .top-right{
              width:100%;
              .pl(20);
              h5{
                .fs(35);
                color:#000;
              }
              p{
                color: #999999;
                height:0.75rem;
                line-height: 0.75rem;
                position: relative;
              }
              p:last-child{
                width: 100%;
                display: flex;
                justify-content: space-between;
                span:last-child{
                  color: #FF512B;
                }
              }
            }
            .top-left{
              img{
                width: 2rem;
                height: 2rem;
                border-radius: 0.2rem;
              }

            }
          }
          .bottom{
            height: 1.3rem;
            color: #000;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
      .footer{
        /*background-color:#fff;*/
        height:1.8rem;
        p{
          .pb(20);
          background-color:#f5f5f5;
          i{
            vertical-align: middle;
          }
        }
      }
    }
  }
</style>



